<template>
  <div>
    <h2>正在获取token2</h2>
    <Arkose
      :public-key="publicKey"
      mode="lightbox"
      @onCompleted="onCompleted($event)"
      @onError="onError($event)"
    />
    <div id="token">
      {{ arkoseToken }}
    </div>

    <input
      type="submit"
      @click="onSubmit()"
      value="Submit"
    >
  </div>
</template>

<script>
import router from '../router.js';
import Arkose from './Arkose.vue';

export default {
  name: 'DashboardComponent',
  components: {
    Arkose
  },
  data () {
    return {
      publicKey: '35536E1E-65B4-4D96-9D97-6ADB7EFF8147',
      arkoseToken: null
    };
  },
  methods: {
    // 加载完成
    mounted () {
      console.log('mounted');
    },
    onCompleted (token) {
      this.arkoseToken = token;
      console.log('token', token);
      this.$http
        .post('/pushtoken', {
          token
        })
        .then((res) => {
          console.log(res);
          // 从url 获取 延迟跳转时间
          const delay = this.$route.query.delay || 5;
          console.log('delay', delay);

          // 延迟跳转
          setTimeout(() => {
            router.replace({ path: '/', query: { delay } });
          }, delay * 1000);
        })
        .catch((err) => {
          console.log(err);
          // 从url 获取 延迟跳转时间
          const delay = this.$route.query.delay || 5;
          console.log('delay', delay);

          // 延迟跳转
          setTimeout(() => {
            router.replace({ path: '/', query: { delay } });
          }, delay * 1000);

          // alert(err);
        });
      // 延迟5秒后跳转
      // setTimeout(() => {
      //   router.replace({ path: '/' });
      // }, 5000);
    },
    onError (errorMessage) {
      alert(errorMessage);
    },
    onSubmit () {
      if (!this.arkoseToken) {
        window.myEnforcement.run();
      }
    }
  }
};
</script>
